import React from "react";
import {Field, reduxForm} from "redux-form";
import {login} from "../../redux/authReducer";
import Textarea, {CreateFields, Input} from "../common/FormsControls/FormsControls";
import {RequiredField} from "../../utils/validation/validation";
import style from "./Login.module.css"
import {connect} from "react-redux";
import {Navigate} from "react-router-dom";
import errorStyle from "../../components/common/FormsControls/formControll.module.css"

const Loginform = ({handleSubmit, error}) => {
    return (
        <div className={style.loginForm}>
            <form onSubmit={handleSubmit}>
                {CreateFields("email", "email", [RequiredField], Input)}
                {CreateFields("Password", "password", [RequiredField], Input, {type:"password"})}
                {CreateFields(null, "rememberMe", [RequiredField], Input, {type:"checkbox"}, "rememberMe")}
                {error && <div className={errorStyle.formError}>
                    {error}
                </div>}

                    <button>Login</button>

            </form>
        </div>
    )
}

const LoginReduxForm = reduxForm({
    form: 'login'
})(Loginform)

const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }

    if(props.isAuthUser){
        return <Navigate to={"/Profile"}/>
    }

    return <div>
        <h1>LOGIN</h1>
        <LoginReduxForm onSubmit={onSubmit}/>
    </div>
}

const mapStateToProps = (state) => ({
    isAuthUser: state.auth.isAuthUser
})

export default connect(mapStateToProps, {login,})(Login);