import React from 'react';
import navbar from "./Navbar.module.css";
import {NavLink} from "react-router-dom";
import store from "../../redux/reduxStore";


const Navbar = (props) => {
    let navLinks = store.getState().navbars.navbar.map(nav => <NavLink id={nav.id} to={nav.path} className={navData =>
        navData.isActive ? navbar.active : navbar.item}>{nav.name}</NavLink>)
    return (
        <nav className={navbar.nav}>
            <div>
                {navLinks}
            </div>
        </nav>

    )
}

export default Navbar;