import {getAuthUserData} from "./authReducer";

const setInitialized = 'SET_INITIALIZED';

const initialState = {
    initialized: false,
}

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case setInitialized:
            return {
                ...state,
                initialized: true,
            }

        default: {
            return state
        }
    }
}
export const initializedSuccess = () => ({type: setInitialized});
export const initializeApp = () => (dispatch) => {
    let dispatchResult = dispatch(getAuthUserData());
    // dispatch(somethinglse())
    // dispatch(somethinglse())
    dispatch(initializedSuccess())
}

export default appReducer