import React, {useState} from "react";
import styles from "../Paginator/Paginator.module.css";


let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanger, portionSize = 10}) => {
    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = (pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1)
    let LeftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rigthPortionPageNumber = portionNumber * portionSize;

    return <div className={styles.pagination}>
        {portionNumber > 1 &&
            <button className={styles.button} onClick={() => {
                setPortionNumber(portionNumber - 1)
            }}>PREV</button>}

        {pages.filter(page => page >= LeftPortionPageNumber && page <= rigthPortionPageNumber)
            .map(page => {
                return <div className={styles.pagination}>
                        <span
                            className={(currentPage === page && styles.selectedPageActive)}
                            onClick={(e) => {
                                onPageChanger(page)
                            }}>
                                 {page}
                    </span>
                </div>
            })}
        {portionCount > portionNumber &&
            <button className={styles.button} onClick={() => {
                setPortionNumber(portionNumber + 1)
            }}>NEXT</button>}

    </div>
}

export default Paginator