const messageReducer = (state = initialMessage, action) => {

    switch (action.type) {
        case addMessage: {
            let body = action.newMessageBody;
            return {
             ...state,
                message: [...state.message, {id: '2', message: body}],
            }
        }

        default: {
            return state
        }
    }
}
export const AddMessgeActionCreator = (newMessageBody) => ({type: addMessage, newMessageBody})
export default messageReducer