import React from "react";
import users from "./Users.module.css";
import userPhoto from "../../assets/images/258-2582267_circled-user-male-skin-type-1-2-icon-male-user-icon.png";
import {Navigate, NavLink} from "react-router-dom";



let User = ({user, followingInProgress, unFollow, follow}) => {
    return <div className={users.user}>
            <div className={users.ListUsers}>
                <NavLink to={"/profile/" + user.id}>
                    <img src={user.photos.small != null ? user.photos.small : userPhoto}/>
                </NavLink>
                <div className={users.information}>
                    <div className={users.name}>{user.name}</div>
                    <div className={users.status}> {user.status}</div>
                </div>
            </div>
            <div className={users.Button}>
                {user.followed
                    ? <button disabled={followingInProgress.some(id => id === user.id)} onClick={() => {
                        unFollow(user.id)
                    }}>UnFollow</button>

                    : <button disabled={followingInProgress.some(id => id === user.id)} onClick={() => {
                        follow(user.id)
                    }}>Follow</button>
                }
            </div>
    </div>}

export default User