import {profileAPI} from "../api/api";

const addPost = 'ADD_POST';
const updateNewPostText = "UPDATE-NEW-POST-TEXT";
const SET_USER_PROFILE = "SET_USER_PROFILE"
const SET_STATUS = "SET_STATUS"
const DELETE_POST = "DELETE_POST"

let initialState = {
    posts: [
        {
            id: "1",
            description: "Тестовый пост ничего нового!",
            likeCount: "2",
            postImg: "https://i.pinimg.com/originals/7a/fe/96/7afe96d9031af93db3696fb15313ffec.jpg"
        },
        {
            id: "2",
            description: "как дела",
            likeCount: "2",
            postImg: "https://i.pinimg.com/originals/3a/f5/03/3af5030c2f4dc691d43cab0c8869b11b.png"
        },
    ],
    textPost: "",
    profile: null,
    status: ""
}

const postReducer = (state = initialState, action) => {
    switch (action.type) {

        case addPost: {
            let newPost = {id: "3", description: action.newPostBody, likeCount: "2", postImg: ""}
            return {
                ...state,
                posts: [...state.posts,  newPost],
            }
        }

        case SET_USER_PROFILE: {
            return {
            ...state, profile: action.profile
            }
        }

        case SET_STATUS: {
            return{
                ...state,
                status: action.status
            }
        }

        case DELETE_POST: {
            return{
                ...state,
                posts: state.posts.filter(p => p.id != action.postId)
            }
        }

        default:
            return state;
    }
}
export const AddPostActionCreator = (newPostBody) => ({type: addPost, newPostBody})
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setStatus = (status) => ({type: SET_STATUS, status})
export const deletePost = (postId) => ({type: DELETE_POST, postId})

export const getProfile = (id) => async (dispatch) => {
    let response = await profileAPI.getProfile(id)
    dispatch(setUserProfile(response.data))
}

export const getStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId)
        dispatch(setStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
        if(response.data.resultCode === 0){
            dispatch(setStatus(status));
        }
}

export default postReducer