import React from 'react';
import profileInfo from "./profileInfo.module.css";
import Loader from "../../common/loader/loader";
import userPhoto from "../../../assets/images/258-2582267_circled-user-male-skin-type-1-2-icon-male-user-icon.png";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";


const ProfileInfo = ({profile, status, updateStatus}) => {
    if(!profile){
        return<Loader/>
    }else {
        return <div className={profileInfo.userPost}>
            <div className="avatar">
                    <img src={profile.photos.large != null ? profile.photos.large : userPhoto}/>
            </div>


            <div className={profileInfo.description}>
                <div>
                    {profile.fullName}
                    <div>
                        <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
                    </div>
                </div>
            </div>

        </div>
    }


}

export default ProfileInfo;