import React from 'react';
import header from "./Header.module.css";
import {NavLink} from "react-router-dom";


const Header = (props) => {
    return (
    <header className={header.header}>
        <img  src='https://yt3.googleusercontent.com/ytc/APkrFKYFNTlYQ0GESNcdi4e_uX0eA7FD4x73HzzbMBze=s900-c-k-c0x00ffffff-no-rj' />
        <div className={header.login}>
            {props.isAuthUser ? <NavLink to={'/login'}><div>{props.login} - <button onClick={props.logout}>logout</button></div></NavLink>
                 : <NavLink to={'/login'}>Login</NavLink>
            }
        </div>

    </header>
)}

export default Header;