import {usersAPI} from "../api/api";
import {updateObjectInArray} from "../utils/object-helpers/helper.jsx";

let FOLLOW = "FOLLOW"
let UNFOLLOW = "UNFOLLOW"
let SET_USERS = "SET_USERS"
let SET_CURRENT_PAGE = "SET_CURRENT_PAGE"
let SET_TOTAL_COUNT = "SET_TOTAL_COUNT"
let TOOGLE_IS_FEETCHING = "TOOGLE_IS_FEETCHING"
let TOOGLE_FOLLOWING_IN_PROGRESS = "TOOGLE_FOLLOWING_IN_PROGRESS"

let initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFeetching: false,
    followingInProgress: []
}

let usersReducer = (state = initialState, action) => {

    switch (action.type) {

        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id" ,{followed:true})
            }

        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id" ,{followed:false})
            }

        case SET_USERS:
            return {
                ...state, users: action.users
            }

        case SET_CURRENT_PAGE:
            return {
                ...state, currentPage: action.currentPage
            }

        case SET_TOTAL_COUNT:
            return {
                ...state, totalUsersCount: action.totalPage
            }
        case TOOGLE_IS_FEETCHING:
            return {
                ...state, isFeetching: action.isFeetching
            }
        case TOOGLE_FOLLOWING_IN_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFeetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }

        default:
            return state
    }

}
export const followSuccess = (userId) => ({type: FOLLOW, userId});
export const unFollowSuccess = (userId) => ({type: UNFOLLOW, userId})
export const setUsers = (users) => ({type: SET_USERS, users});
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})
export const setTotalCount = (totalPage) => ({type: SET_TOTAL_COUNT, totalPage})
export const toggleIsFeetching = (isFeetching) => ({type: TOOGLE_IS_FEETCHING, isFeetching})
export const toggleFollowingProgress = (isFeetching, userId) => ({
    type: TOOGLE_FOLLOWING_IN_PROGRESS,
    isFeetching,
    userId
})


export const requestUsers = (Page, pageSize) => {
    return async (dispatch) => {
        dispatch(toggleIsFeetching(true))
        dispatch(setCurrentPage(Page))

        let data = await usersAPI.getUsers(Page, pageSize)
        dispatch(toggleIsFeetching(false))
        dispatch(setUsers(data.items))
        dispatch(setTotalCount(data.totalCount))

    }
}

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, userId))
    let responce = await apiMethod(userId)
    if (responce.data.resultCode === 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleFollowingProgress(false, userId))
}

export const follow = (userId) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userId, usersAPI.followUsers.bind(usersAPI), followSuccess)
    }
}

export const unFollow = (userId) => {
    return async(dispatch) => {
        followUnfollowFlow(dispatch, userId, usersAPI.unFollowUsers.bind(usersAPI), unFollowSuccess)
    }
}

export default usersReducer;