import React from "react";
import users from "./Users.module.css";
import Paginator from "../common/Paginator/Paginator.jsx";
import User from "./User.jsx";



let Users = ({currentPage, totalItemsCount, pageSize, onPageChanger, ...props}) => {
    return <div className={users.background}>
        <Paginator currentPage={currentPage} onPageChanger={onPageChanger} totalItemsCount={totalItemsCount} pageSize={pageSize} />

        {
            props.users.map(user => <User user={user}
                                       followingInProgress={props.followingInProgress}
                                       unFollow={props.unFollow}
                                       follow={props.follow}
                                       key={user.id}
                        />
            )
        }
    </div>
}

export default Users