import React from "react";
import style from "./formControll.module.css"
import {RequiredField} from "../../../utils/validation/validation.js";
import {Field} from "redux-form";
 const Textarea = ({input, meta, ...props}) => {
     const hasError = meta.touched && meta.error
     return(
         <div>
             <div>
                 <textarea
                     className={hasError ? style.error : style.textarea}
                     placeholder={hasError ? meta.error : ""}
                     {...input} {...props}/>
             </div>
         </div>
                     )
                 }

export const Input = ({input, meta, ...props}) => {
    const hasError = meta.touched && meta.error && <span>Error</span>
    return(
        <div>
            <div>
                <input
                    className={hasError ? style.error : style.input}

                    {...input} {...props}/>
            </div>
        </div>
    )
}

export const CreateFields = (placeholder, name, validators, component, props = {}, text = "") => {
    return<div>
        <Field
            placeholder={placeholder}
            name={name}
            validate={validators}
            component={component}
            {...props}
        />{text}
    </div>
}

export default Textarea;
