import React from 'react';
import post from"./post.module.css";
import MyStylePost from "../MyPost.module.css";



let Post = (props) => {



    return (
        <div className={post.item}>
            <div id={props.id}>
                <p className={post.description}>{props.postText}</p>
                <img className={MyStylePost.postImg} src={props.img}/>
                <div className={post.reaction}>
                    <img src="https://icon-library.com/images/like-png-icon/like-png-icon-26.jpg"/>
                    <img src="https://static.insales-cdn.com/images/collections/1/6579/1907123/comments.png"/>
                    <img src="https://grizly.club/uploads/posts/2023-02/1676122818_grizly-club-p-repost-klipart-10.png"/>
                </div>
            </div>
        </div>

    )
}

export default Post;